html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Jost", sans-serif;
  background-color: var(--color-white);
  font-weight: 500;
  color: var(--color-dark);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
}

@media (max-width: 576px) {
  .container {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  opacity: 0;
  background-color: currentColor;
  transition: opacity 0.2s;
}

.main-link--white {
  color: var(--color-white);
}

.main-link:hover::before {
  opacity: 1;
}

.container-narrow {
  max-width: 1406px;
}

.to-top {
  padding: 14px 18px;
  position: fixed;
  right: 2%;
  bottom: 5%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transition: scale 0.3s;
}

.to-top svg {
  width: 12px;
  height: 21px;
}

.to-top:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

@media (max-width: 576px) {
  .header__container {
    padding: 20px 15px;
  }
}

.header__shop-nav {
  margin-left: 250px;
}

@media (max-width: 1200px) {
  .header__shop-nav {
    margin-left: 0;
  }
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

@media (max-width: 576px) {
  .logo {
    width: 90px;
    height: 18px;
  }
}

.catalog-header {
  padding-top: 38px;
}

@media (max-width: 576px) {
  .catalog-header {
    padding-top: 0;
  }
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 10px;
  text-transform: uppercase;
  background-color: var(--color-dark);
}

.free-delivery__btn {
  position: absolute;
  z-index: 110;
  right: 40px;
  top: 50%;
  width: 12px;
  height: 12px;
  cursor: pointer;
  transform: translateY(-50%);
}

.free-delivery__btn svg {
  width: 100%;
  height: 100%;
  fill: var(--color-white);
}

@media (max-width: 576px) {
  .free-delivery {
    display: none;
  }
}

.card-page .header {
  padding-top: 0;
}

@media (max-width: 1200px) {
  .nav {
    position: fixed;
    left: -150vw;
    top: 0;
    width: 80%;
    padding: 10% 0;
    height: 100vh;
    background-color: #fff;
    transition: left 0.3s;
  }
}

.nav__close {
  display: none;
}

@media (max-width: 1200px) {
  .nav__close {
    display: block;
    position: absolute;
    right: 10%;
    top: 5%;
  }
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 18px;
}

.burger::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
}

.burger__line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
  transform: translateY(-50%);
}

.burger::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
}

@media (max-width: 1200px) {
  .burger {
    display: block;
  }
}

.nav__list {
  display: flex;
}

@media (max-width: 1200px) {
  .nav__list {
    display: block;
  }
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

@media (max-width: 1200px) {
  .nav__item:not(:last-child) {
    margin-right: 0;
  }
}

.nav__link {
  position: relative;
  font-size: 15px;
}

@media (max-width: 1200px) {
  .nav__link:hover::before {
    opacity: 0;
  }
}

.nav__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  opacity: 0;
  background-color: var(--color-dark);
  transition: opacity 0.2s;
}

.nav__link:hover::before {
  opacity: 1;
}

.nav__link--current::before {
  opacity: 1;
}

@media (max-width: 1200px) {
  .nav__link--current::before {
    opacity: 0;
  }
}

@media (max-width: 1200px) {
  .nav__link {
    width: 100%;
    display: inline-block;
    padding: 10px 10%;
    border-bottom: 1px solid #bfbfbf;
    font-size: 20px;
  }
}

.nav--visible {
  left: 0;
  transform: left 0.3s;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link:hover {
  opacity: 0.7;
  transition: opacity 0.3s;
}

.shop-nav__link--user {
  background-image: url("../../img/user.png");
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

@media (max-width: 576px) {
  .shop-nav__item {
    margin-right: 15px;
  }
}

.cart {
  width: auto;
  height: auto;
}

.cart__icon {
  background-image: url("../../img/cart.png");
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../img/search.png");
}

.search-form__btn:hover {
  opacity: 0.7;
  transition: opacity 0.3s;
}

.quantity {
  display: inline-flex;
  align-items: center;
  padding: 0 5px;
  height: 16px;
  border-radius: 25px;
  font-size: 10px;
  font-weight: 700;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.hero {
  position: relative;
  height: 100vh;
  height: 1080px;
  min-height: 700px;
  background-color: var(--quil-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 40px;
  max-width: 540px;
  font-size: 72px;
  font-weight: 700;
}

@media (max-width: 1200px) {
  .hero__title {
    max-width: 500px;
    font-size: 50px;
  }
}

@media (max-width: 576px) {
  .hero__title {
    font-size: 45px;
  }
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

@media (max-width: 1200px) {
  .hero__descr {
    font-size: 14px;
  }
}

.hero__image {
  position: absolute;
  right: 262px;
  bottom: 0;
  max-height: 90%;
}

@media (max-width: 1700px) {
  .hero__image {
    right: 100px;
  }
}

@media (max-width: 1500px) {
  .hero__image {
    right: 50px;
  }
}

@media (max-width: 1200px) {
  .hero__image {
    right: 0;
    width: 350px;
  }
}

@media (max-width: 768px) {
  .hero__image {
    display: none;
  }
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

@media (max-width: 1500px) {
  .hero__content {
    padding: 0 100px;
  }
}

@media (max-width: 1200px) {
  .hero__content {
    padding: 0;
  }
}

@media (max-width: 1700px) {
  .hero {
    height: 700px;
  }
}

@media (max-width: 1200px) {
  .hero {
    height: 600px;
    min-height: auto;
  }
}

.banner-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag,
.hero-pag {
  bottom: 45px !important;
}

.banner-pag .swiper-pagination-bullet,
.hero-pag .swiper-pagination-bullet {
  margin: 0 12px !important;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active,
.hero-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before,
.hero-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  z-index: -1;
  border-radius: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
}

.marketing {
  position: absolute;
  z-index: 10;
  display: flex;
  left: 40px;
  bottom: 50px;
  padding: 10px;
  width: 280px;
  background-color: var(--color-white);
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  transform: translateX(-150vw);
  transition: transform 0.5s ease-out;
}

.marketing--visible {
  transform: translateX(0);
  transition: transform 0.5s ease-out;
}

.marketing__text {
  margin-left: 10px;
}

.marketing__image {
  flex-shrink: 0;
}

.marketing__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.marketing__descr {
  color: var(--color-gray);
  display: block;
  font-size: 11px;
}

.marketing__title {
  display: block;
  font-size: 15px;
  margin: 0;
  margin-bottom: 7px;
  min-height: 34px;
  padding-right: 20px;
}

.marketing__when-from {
  display: block;
  font-size: 11px;
  padding-right: 30px;
}

.marketing__btn {
  position: absolute;
  cursor: pointer;
  right: 10px;
  padding: 0;
  top: 10px;
  width: 10px;
  height: 10px;
  background-image: url("../../img/close.png");
  background-repeat: no-repeat;
  background-size: cover;
}

@media (max-width: 576px) {
  .marketing {
    left: 10px;
  }
}

.banners__container {
  padding-top: 50px;
  padding-bottom: 30px;
}

@media (max-width: 576px) {
  .banners__container {
    padding-bottom: 0;
  }
}

.banners__list {
  display: flex;
}

@media (max-width: 1200px) {
  .banners__list {
    flex-wrap: wrap;
  }
}

.banners__item {
  width: 33%;
}

.banners__item:not(:last-child) {
  margin-right: 1%;
}

@media (max-width: 1200px) {
  .banners__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.banners-article {
  padding: 54px 60px;
  padding-right: 50%;
  min-height: 290px;
  background-color: #e9e4df;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.banners-article-midseasons {
  background-image: url("../../img/art1.jpg");
}

.banners-article-summer {
  background-image: url("../../img/art2.jpg");
}

.banners-article-accs {
  background-image: url("../../img/art3.jpg");
}

.banners-article__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
}

@media (max-width: 1200px) {
  .banners-article {
    padding: 50px 20px;
  }
}

@media (max-width: 768px) {
  .banners-article {
    padding-right: 100px;
  }
}

.main-products__container {
  padding-top: 70px;
  padding-bottom: 30px;
}

@media (max-width: 1200px) {
  .main-products__container {
    margin-bottom: 0px;
  }
}

.main-products__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
}

.main-products__item {
  margin-right: 30px;
}

@media (max-width: 576px) {
  .main-products__item {
    margin: 0 10px;
  }
}

.main-products__btn {
  font-size: 24px;
}

.main-products__btn--current::before {
  opacity: 1;
}

@media (max-width: 768px) {
  .main-products__btn {
    font-size: 20px;
  }
}

@media (max-width: 576px) {
  .main-products__btn {
    font-size: 15px;
  }
}

.main-products__btn-center {
  text-align: center;
}

.main-products__more {
  font-size: 13px;
  font-weight: 500;
  color: var(--color-gray);
  margin-top: 95px;
  display: inline-flex;
  align-items: center;
  flex-direction: column;
  transition: transform 0.2s;
